@media (max-width: 1400px) {
  .home-content{
    min-height: 500px;
  }
  .main-img {
    width: 312px;
    height: 291px;
    &:after {
      width: 40px;
      height: 70px;
      bottom: 48px;
    }
  }

  .small-img {
    width: 100px;
    height: 149px;
    &:before {
      width: 70px;
      height: 50px;
      left: -63px;
      bottom: -10px;

    }
    &:after {
      width: 33px;
      height: 85px;
      bottom: -19px;
    }
  }
  .main-title {
    font-size: 67px;
  }
  .sub-title {
    margin: 0 0 35px 220px;
    font-size: 56px;
  }
  .home-title-descr {
    margin-left: 325px;
    font-size: 17px;
  }
  .contact{
    .small-img{
      height: 97px;
    }
  }
}

@media (max-width: 1180px) {
  .home-content {
    max-width: 900px;
    margin: 0 auto;
  }
  .main-title {
    font-size: 55px;
  }
  .sub-title {
    margin: 0 0 25px 176px;
    font-size: 44px;
  }
  .home-title-descr {
    margin-left: 264px;
    font-size: 12px;
  }
  .main-nav {
    width: 58%;
  }
  .info-item img {
    max-width: 85%;
  }
}

@media (max-width: 992px) {
  .section-title {
    font-size: 46px;
  }
  .section-descr {
    font-size: 16px;
  }
  .info {
    .section-title {
      margin-bottom: 24px;
    }
    .section-descr {
      margin: 0 0 44px;
    }
    .txt-wrapper {
      width: 60%;
    }
    .img-wrapper {
      width: 40%;
    }
  }
  .section-list {
    .item-title {
      margin-bottom: 15px;
      font-size: 18px;
    }
    li p {
      font-size: 13px;
    }
  }

}

@media (max-width: 867px) {
  .title-wrapper {
    flex-basis: 100%;
    margin-bottom: 250px;
    text-align: center;
    .main-title, .sub-title, .home-title-descr {
      margin-left: 0;
    }
  }
  .main-img {
    display: none;
  }
  .small-img {
    left: 79px;
  }
  .menu-toggle {
    display: flex;
    height: 50px;
    align-items: center;
  }
  .main-nav {
    display: none;
  }
  .main-nav.active{
    display: inline-block;
  }
  .about {
    padding-bottom: 0;
  }
  .features-wrapper {
    flex-wrap: wrap;
  }
  .item {
    width: calc(100% / 3);
    margin-bottom: 60px;
  }
}

@media (max-width: 668px) {
  .title-wrapper{
    margin-bottom: 180px;
  }
  .main-title {
    font-size: 45px;
  }
  .sub-title {
    font-size: 38px;
  }
  .home-title-descr {
    font-size: 10px;
  }
  section {
    padding: 90px 0;
  }
  .features-wrapper {
    padding: 50px 0;
  }
  .info-item {
    flex-wrap: wrap;
    .txt-wrapper, .img-wrapper {
      width: 100%;
    }
    .img-wrapper {
      margin-bottom: 45px;
    }
    &:first-child {
      .img-wrapper {
        order: 0;
      }
      .txt-wrapper {
        order: 1;
      }
    }
    .section-title, .section-descr {
      margin: 0 auto 28px;
      text-align: center;
      max-width: 100%;
    }
  }
  .contact{
    .small-img{
      display: none;
    }
  }
}

@media (max-width: 547px) {
  .item {
    width: 50%;
  }
}

@media (max-width: 468px) {
  .logo{
    width: 100px;
    height: 27px;
  }
  .menu-toggle{
    height: 27px;
  }

  .main-title {
    font-size: 33px;
  }

  .sub-title{
    margin-bottom: 18px;
    font-size: 28px;
  }

  .home-title-descr {
    font-size: 8px;
  }
  section {
    padding: 48px 0;
  }
  .section-title {
    margin-bottom: 24px;
    font-size: 28px;
  }
  .section-descr {
    font-size: 14px;
  }
  .about {
    padding-top: 45px;
  }
  .features-wrapper {
    padding: 0;
  }
  .item {
    width: 100%;
    .img-wrapper {
      min-height: 0;
    }
  }
  .item-title {
    font-size: 16px;
  }
  .item-descr {
    font-size: 14px;
  }
  .section-list {
    margin-left: 0;
    li {
      padding-left: 15px;
    }
  }
  .publishers {
      padding-top: 45px;
    }

}

