@import "reset";
@import "fonts";

$color: #21303d;
$theme-col: #00ff9c;
$dark-col: #12212f;

@mixin bg {
  background: {
    repeat: no-repeat;
    position: center;
    size: cover;
  }
}

@mixin vert-align {
  top: 50%;
  transform: translateY(-50%);
}

@mixin input-placeholder {
  &.placeholder {
    @content;
  }
  &:-moz-placeholder {
    @content;
  }
  &::-moz-placeholder {
    @content;
  }
  &:-ms-input-placeholder {
    @content;
  }
  &::-webkit-input-placeholder {
    @content;
  }
}

html {
  height: 100%;
}

body {
  height: 100%;
  font-family: "Montserrat", sans-serif;
}

* {
  box-sizing: border-box;
}

a, a:hover {
  text-decoration: none;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  content: " ";
  clear: both;
  font-size: 0;
  height: 0;
}

.left {
  float: left;
}

.right {
  float: right;
}

.text-center {
  text-align: center;
}

.container {
  max-width: 1570px;
  margin: 0 auto;
  padding: 0 15px;
}

section {
  padding: 116px 0;
  overflow: hidden;
}

.home, .contact, .mobile-nav {
  @include bg;
  position: relative;
  background-color: #12212f;
  background-image: url("../images/vector-smart-object.png");
}

.rocket {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background-image: url("data:image/svg+xml;base64,");
  z-index: 1;
  animation: rocket 15s infinite linear;
}

.header {
  padding: 40px 0;
}

.logo {
  @include bg;
  display: inline-block;
  width: 175px;
  height: 50px;
  background-image: url(data:image/svg+xml;base64,);
}

.main-nav {
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 47%;
  &-link {
    display: inline-block;
    font-size: 18px;
    color: #fff;
    transition: all 0.3s;
    &:hover, &:focus, &:active, &.active {
      color: $theme-col;
    }
  }
}

.menu-toggle {
  display: none;
  position: relative;
  z-index: 10;
}

.title-wrapper {
  margin-bottom: 100px;
  position: relative;
  z-index: 3;
}

.home {
  padding: 0;
  overflow: hidden;
  .main-title, .sub-title, &-title-descr {
    color: #fff;
  }
  &-wrapper {
    position: relative;
    z-index: 2;
  }
  &-content {
    max-width: 1570px;
    min-height: 750px;
    margin: 0 auto;
    position: relative;
    height: calc(100vh - 130px);
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  &-title-descr {
    margin-left: 383px;
    font-size: 22px;
  }
}

.main-title {
  margin: 0 0 15px 35px;
  font-size: 90px;
}

.sub-title {
  margin: 0 0 45px 280px;
  font-size: 80px;
  span {
    color: $theme-col;
  }
}

.main-img {
  @include bg;
  position: absolute;
  width: 612px;
  height: 572px;
  right: 35px;
  bottom: 50px;
  top: 50%;
  transform: translateY(-50%);
  background-image: url(data:image/svg+xml;base64,);
  &:after {
    @include bg;
    animation: main-right-rotate 2.5s infinite linear alternate;
    content: "";
    position: absolute;
    width: 80px;
    height: 140px;
    bottom: 96px;
    right: -25px;
    background-image: url(data:image/png;base64,);
  }
  animation: main-rotate 3s infinite linear alternate;
}

.small-img {
  @include bg;
  position: absolute;
  width: 159px;
  height: 242px;
  bottom: 50px;
  left: 100px;
  background-image: url(data:image/svg+xml;base64,);
  animation: small-rotate 2.5s infinite linear alternate;
  &:before {
    @include bg;
    content: "";
    position: absolute;
    bottom: -20px;
    left: -126px;
    width: 139px;
    height: 98px;
    background-image: url(data:image/png;base64,);
    animation: small-left-rotate 1.5s infinite linear alternate;
  }
  &:after {
    @include bg;
    content: "";
    position: absolute;
    bottom: -38px;
    right: -20px;
    width: 65px;
    height: 170px;
    background-image: url(data:image/png;base64,);
    animation: small-right-rotate 4s infinite linear alternate;
  }
}

.about {
  padding: 90px 0;
  background-color: #8afcd0;
}

.section-title {
  margin: 0 auto 35px;
  max-width: 500px;
  font-size: 55px;
  color: $color;
  text-align: center;
}

.section-descr {
  margin: 0 auto 35px;
  max-width: 500px;
  font: {
    family: "Noto Sans", sans-serif;
    size: 17px;
    style: italic;
  }
  line-height: 2;
  color: $dark-col;
  text-align: center;
}

.features-wrapper {
  display: flex;
  padding: 88px 0;
}

.item {
  width: calc(100% / 6);
  text-align: center;
  color: $dark-col;
  .img-wrapper {
    position: relative;
    min-height: 126px;
    margin-bottom: 40px;
    &:hover,
    &:focus {
      animation-name: spaceboots;
      animation-duration: 0.8s;
      transform-origin: 50% 50%;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }
  }
  img {
    display: inline-block;
    vertical-align: middle;
  }
  &-title {
    margin-bottom: 10px;
    font: {
      size: 19px;
      weight: 700;
    }
  }
  &-descr {
    font-size: 17px;
  }
}

.info {
  .section-descr {
    margin: 0 0 64px 0;
  }
  .section-title {
    margin: 0 0 35px 0;
  }
  &-item {
    display: flex;
    align-items: center;
    .img-wrapper{
      text-align: center;
    }
    .section-title {
      text-align: left;
      color: #f2a81a;
    }
    .section-descr {
      text-align: left;
    }
    div {
      width: 50%;
    }
    img {
      max-width: 405px;
    }
    .txt-wrapper {
      text-align: left;
    }
  }
  .publishers{
    padding-top: 100px;
  }
}

.section-list {
  max-width: 560px;
  margin-left: 20px;
  padding-left: 33px;
  list-style-image: url("data:image/png;base64,");
  li {
    margin-bottom: 21px;
    padding-left: 50px;
    p {
      font: {
        size: 14px;
        family: "Noto Sans", sans-serif;
      }
      line-height: 2;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .item-title {
    margin-bottom: 25px;
    color: $color;
  }

}

.contact {
  .section-title {
    color: #fff;
  }
  .section-descr {
    margin-bottom: 60px;
    color: #fff;
  }
  .container{
    position: relative;
  }
  .small-img{
    height: 151px;
    left: auto;
    right: 50px;
    bottom: -55px;
    background-image: url("data:image/png;base64,");
  }

}

.contact-form {
  max-width: 493px;
  margin: 0 auto;
  text-align: center;
  font-size: 0;
}

.selectric-inp{
  margin-bottom: 27px;
}

.inp {
  margin-bottom: 27px;
  padding: 11px;
  border-radius: 3px;
  border: none;
  font: {
    family: "Noto Sans", sans-serif;
    size: 14px;
  }

  @include input-placeholder {
    color: #8e919f;
  }
}

.name, .email {
  width: 48%;
}

.name{
  margin-right: 4%;
}

.message {
  width: 100%;
  height: 89px;
  resize: none;
}

