  width: 100%;
  color: $dark-col;
  appearance: none;
  background-image: url("data:image/png;base64,");
  background-position: calc(100% - 11px) center;
  background-repeat: no-repeat;
}*/

.selectric{
  background: #fff;
  border-radius: 3px;
  .label{
    text-align: left;
  }
  .button{
    background: url("data:image/png;base64,") center center no-repeat;
    &:after{
      display: none;
    }
  }
  &-items{
    li{
      text-align: left;
      //background: #fff;
      &:hover{
        //background-color: #f8f8f8;
      }
      &.highlighted{
        //background-color: $theme-col;
      }
      &.selected{
        background-color: #68ffc4;
      }
    }
  }
}

.btn {
  position: relative;
  padding: 15px 66px;
  background-color: $theme-col;
  color: $dark-col;
  border: none;
  border-radius: 28px;
  cursor: pointer;
  font-size: 17px;
  overflow: hidden;
  transform: perspective(1px) translateZ(0);
  transition-property: color;
  transition-duration: 0.3s;
  &:before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: #f2a81a;
    border-radius: 100%;
    transform: scale(0);
    transition-property: transform;
    transition-duration: 0.3s;
    transition-timing-function: ease-out;
  }
  &:active, &:hover, &:focus {
    border: none;
    outline: none;
    &:before {
      transform: scale(2);
    }
  }
}

.mobile-nav{
  display: none;
  position: fixed;
  padding: 50px 67px;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  box-shadow: 1px 1px 3px rgba(0,0,0,.5);
  z-index: 5;
  &.active{
    display: block;
  }
  &-wrapper{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
  }

  &-content{
    width: 100%;
    max-width: 420px;
    font-size: 0;
  }
  .main-nav{
    max-width: 420px;
    &-link{
      display: block;
      background-color: $theme-col;
      color: $dark-col;
      padding: 15px 15px 10px;
      margin-bottom: 8px;
      border-radius: 100px;
      text-align: center;
      font-size: 15px;
    }
  }
}

.nav-close {
  @include bg;
  position: absolute;
  width: 16px;
  height: 16px;
  right: 15px;
  top: 59px;
  background-image: url(data:image/png;base64,);
}

@keyframes rocket {
  50% {
    bottom: 60%;
    left: 40%;
    transform: rotate(30deg);
    width: 90px;
    height: 90px;
  }
  to {
    bottom: 100%;
    left: 100%;
    width: 50px;
    height: 50px;
  }
}

@keyframes main-rotate {
  to {
    transform: translateY(-50%) rotate(3deg);
  }
}

@keyframes main-right-rotate {
  to {
    transform: rotate(-15deg);
  }
}

@keyframes small-rotate {
  to {
    transform: rotate(3deg);
  }
}

@keyframes small-left-rotate {
  to {
    transform: rotate(-5deg);
  }
}

@keyframes small-right-rotate {
  to {
    transform: rotate(10deg);
  }
}

@keyframes spaceboots {
  0% {
    transform: translate(2px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(0px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(2px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(2px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

@import "media";

